import { Button, Row, Col } from "react-bootstrap"
const EmptyWithVideo = ({title, desc, button, vimeoCode, isFullScreen}) => {
       return <div className="create_page_ rounded-0 mx-auto f-center w-100 h-100">
                     <Row className="py-5 text-center w-100 justify-content-center">
                            <Col sm={10} lg={8} xl={7} xxl={6}>
                                   <h2 className="text-center h3 mb-4">{title}</h2>
                                   <div className="video-border position-relative mb-5 border-lg overflow-hidden rounded-3" style={{paddingBottom:'55%'}}>
                                          <div className="position-absolute start-0 top-0 video_play w-100 h-100">
                                                 <iframe className="w-100 h-100" src={`https://player.vimeo.com/video/${vimeoCode}`} width="640" height="355" frameborder="0" allow="autoplay; fullscreen; picture-in-picture" allowfullscreen></iframe>
                                          </div>
                                   </div>
                                   <p className="fs-8 text-muted fw-medium">{desc}</p>
                                   <Button onClick={() => button.action()} size={isFullScreen?'lg':'md'} className="my-3">{button.text}</Button>
                            </Col>
                     </Row>
              </div>
}

export default EmptyWithVideo