import React, { Component } from 'react'
import $ from 'jquery'
import { DashboardModal } from "@uppy/react";
import { Image, Button, Form, InputGroup, ToggleButtonGroup, ToggleButton } from 'react-bootstrap';
import { SHOPON_EMBED_URL } from '../../../../../actions/api';
import UppyUpload from '../../../../../utils/uppyUpload';
import { TEMP_URL } from '../../../../../actions/constants';
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import { checkPermission } from '../../../../../actions/collaboratorActions';
import RequestButtonHOC from '../../CommonComponents/RequestButtonHOC';


export default class Aside extends Component {
    state = { modalOpen: false }
    uppyUploader = new UppyUpload()

    componentDidMount() {
        const { changeProfile } = this.props;

        this.uppyUploader.uppy.on('transloadit:result', (stepName, result) => {
            changeProfile(`${TEMP_URL}${result.id}.${result.ext}`)
            setTimeout(() => { this.uppyUploader.uppy.cancelAll(); this.setState({ modalOpen: false }) }, 300);
        })
    }


    onClickToOpen = () => {
        if(checkPermission('publish', 'shopon',true)){
            this.setState({ modalOpen: true }, () => $(".uppy-Dashboard-close").click(() => {
                this.uppyUploader.uppy.cancelAll()
                this.setState({ modalOpen: false })
            }))
        }
    }

    onClickToClose = () => {
        this.uppyUploader.uppy.cancelAll()
        this.setState({ modalOpen: false })
    }

    componentWillUnmount() {
        this.uppyUploader.uppy.cancelAll()
        this.uppyUploader.uppy.close({ reason: 'unmount' })
        this.setState({ modalOpen: false })

    }

    doneButtonHandler = () => {
        this.uppyUploader.uppy.cancelAll()
        this.onClickToClose()
    }

    render() {
        const { title, description, profile, changeTitle, changeDescription, slug, changeSlug, slugError, slugMsg, errorTitle, errorDescription, changeTheme, theme_type, footer_action2, copied, copyText, ifchanges, selectedWall } = this.props;
        return (
            selectedWall ? <>
                <div className='border-bottom px-4 py-4 mb-4'>
                    <h6 className='mb-2'>Get Started With ShopOn.bio</h6>
                    <p className='mb-3 fs-9 text-muted'>Brand Bio in 300 char 👇 Craft a compelling brand bio! 🌟 Showcase your uniqueness, values, and mission. Inspire your audience with brevity. Highlight what sets you apart! 🔥 #BrandStory</p>
                    <div className="d-flex align-items-center mb-4">
                        <div className="d-flex align-items-center flex-shrink-0 position-relative me-3">
                            <div id="shop_logo" className="symbol symbol-50 rounded-circle overflow-hidden cursor-pointer" onClick={this.onClickToOpen}>
                                {!profile && !title ?
                                    <FontAwesomeIcon icon={'user'} iconClass="display-1" />
                                    : profile ?
                                        <Image src={`${profile}`} height={50} width={50} alt={title} fluid />
                                        :
                                        <span className='f-center text-uppercase h-34px w-34px fs-2 text-gray-100'>{title?.charAt(0).toUpperCase()}</span>}
                            </div>
                            <Button variant='primary' className='f-center position-absolute rounded-circle h-20px w-20px p-0 fs-10' style={{ bottom: -2, right: -2 }} onClick={this.onClickToOpen}>
                                <FontAwesomeIcon type={'solid'} icon={'plus'} iconClass="me-0" />
                            </Button>
                        </div>
                        <div>
                            <Form.Control id='shopon_title' className={`${errorTitle ? 'is-invalid' : ''}`} value={title} onChange={(e) => changeTitle(e.target.value)} disabled={!checkPermission('publish', 'shopon',true,true)}/>
                            <span className='invalid-feedback'>{errorTitle ? "This field is required " : ''}</span>
                        </div>
                    </div>

                    <Form.Control id='shopon_desc' as="textarea" className={`${errorDescription ? 'is-invalid' : ''}`} maxLength={300} onChange={(e) => { if (e.target.value.length < 151) changeDescription(e.target.value); else changeDescription((e.target.value).substring(0, 300)) }} value={description} placeholder="Bio" style={{ minHeight: 120 }} disabled={!checkPermission('publish', 'shopon',true,true)} />
                    <div className="f-between">
                        <div className="pt-1" style={{ color: 'red', fontSize: '10px' }}>{errorDescription ? 'This field is required' : ''}</div>
                        <div className="d-flex justify-content-end">
                            <span className="fs-10 text-muted pt-1">{description?.length}/300</span>
                        </div>
                    </div>
                </div>
                <div className='px-4 mb-4'>
                    <Form.Label className="">Choose your ShopOn.bio Username</Form.Label>
                    <div className="mb-4">
                        <InputGroup className={slugError == 'error' ? 'is-invalid' : 'is-valid'}>
                            <InputGroup.Text>
                                shopon.bio/
                            </InputGroup.Text>
                            <Form.Control id='shopon_url' className={slugError == 'error' ? 'is-invalid' : 'is-valid'} maxLength="255" value={slug} onChange={(e) => changeSlug(e.target.value)} disabled={!checkPermission('publish', 'shopon',true,true)}/>
                            {slugError == 'error' ? <span className='invalid-feedback'>{slugMsg ? slugMsg : 'URL is already taken'}</span> : ''}
                            {slug?.length > 2 && slugError == 'success' ? <span className='valid-feedback'>URL is available</span> : ''}
                        </InputGroup>

                    </div>
                    <div className='d-flex flex-column mb-2'>
                        <Form.Label>Theme type</Form.Label>
                        <ToggleButtonGroup type="radio" name="options" value={theme_type} onChange={changeTheme}>
                            <ToggleButton variant='secondary text-nowrap' id="mode-2" value={1} disabled={!checkPermission('publish', 'shopon',true,true)}>
                                <FontAwesomeIcon type={'solid'} icon={'sun-bright'} iconClass="me-1" />
                                Light
                            </ToggleButton>
                            <ToggleButton variant='secondary text-nowrap' id="mode-3" value={2} disabled={!checkPermission('publish', 'shopon',true,true)}>
                                <FontAwesomeIcon type={'solid'} icon={'moon'} iconClass="me-1" />
                                Dark
                            </ToggleButton>
                        </ToggleButtonGroup>
                    </div>
                    <RequestButtonHOC title={'Request Customisation'}>
                        <Button variant="secondary" className={`w-100 mt-4`}>
                                <FontAwesomeIcon icon={'lightbulb'}/>
                                Request Customisation
                        </Button>
                    </RequestButtonHOC>
                </div>
                {footer_action2 && !ifchanges ?
                    <div className="bg-gray-200 position-sticky border-top border-bottom bottom-0 px-4" style={{ zIndex: 9 }}>
                        <InputGroup className="py-5">
                            <a id='shopon_url' className='form-control text-nowrap  fw-medium text-ellipsis' href={`${SHOPON_EMBED_URL}/${slug}`} target="_blank">{`${SHOPON_EMBED_URL}/${slug}`}</a>
                            <Button id='copy_url_btn' variant='secondary' className={`${ifchanges ? 'disabled' : ''}`} onClick={copyText}>{copied ? `Copied ` : 'Copy'}</Button>
                        </InputGroup>
                    </div> : ''}

                {
                    this.state.modalOpen ? <DashboardModal
                        animateOpenClose={true}
                        proudlyDisplayPoweredByUppy={false}
                        inline={false}
                        showProgressDetails={true}
                        height={750}
                        browserBackButtonClose={true}
                        showRemoveButtonAfterComplete={false}
                        uppy={this.uppyUploader.uppy}
                        open={true}
                        onRequestCloseModal={this.onClickToClose}
                        closeAfterFinish={true}
                        doneButtonHandler={this.doneButtonHandler}
                        plugins={["Instagram", "GoogleDrive", "Url", "Webcam", "ImageEditor"]}
                    /> : null
                }
            </> : <div></div>
        )
    }
}
