import React, { Component } from 'react'
import { Container, Card, Button, ToggleButtonGroup, ToggleButton, Image, Dropdown } from 'react-bootstrap';
import Aside from './Components/Aside'
import { Loader } from '../CommonComponents/Loader';
import EmptyWithVideo from '../CommonComponents/Empty/EmptyWithVideo';
import { connect } from 'react-redux';
import { CHANGE_SHOPON_WALL, GET_ALL_SHOPON_LIST, SHOPON_SLUG_AVAILABILITY, UPDATE_SHOPON } from '../../../../actions/shoponAction';
import { getUserInfo_afterChanges } from '../../../../actions/loginActions';
import { SHOPON_BRAND_DATA_REDUCER, SHOPON_DESCRIPTION_REDUCER, SHOPON_LOGO_REDUCER, SHOPON_TITLE_REDUCER, SHOPON_TYPE_REDUCER, TOGGLE_ADD_SHOPON } from '../../../../reducer/keys';
import { IS_API_ACCESS_LIVE, SHOPON_EMBED_URL } from '../../../../actions/api';
import CardAside from '../CommonComponents/CardAside';
import ContentSwitcher from '../CommonComponents/ContentSwitcher/';
import { Link } from 'react-router-dom';
import { baseUrl, FontAwesomeIcon } from '../../../../Assets/AssetsHelper';
import "./shop-on-bio.scss"
import withRouter from '../../../routes/WithRouter';
import WebsiteSwitcher from '../CommonComponents/WebsiteSwitcher';
import { debounce } from '../../../../utils';
import _ from 'lodash';
import EmptyWithIcon from '../CommonComponents/Empty/EmptyWithIcon';
import PublishSubheader from '../CommonComponents/PublishSubheader'

class ShopOnBio extends Component {
    state = { footer_action1: true, footer_action2: false, lightMode: true, darkMode: false, spinner: false, activeTab: 1, activeGallery: [], shopOnbio: [], title: '', description: '', profile: '', imgFile: '', slug: '', currentSlug: '', slugError: "", theme_type: 1, errorTitle: false, errorDescription: false, ifchanges: false, copied: false, shoponLoading: true, analytics_shopon: false, slugMsg: '', selectedWall: '', isMobile: true, filterToggle: false, selectLoader: true }
    componentDidMount() {
        if (!(this.props.shopOnData?.shopOnList && this.props.shopOnData?.shopOnList.length)) this.props.GET_ALL_SHOPON_LIST()
        document.querySelector('.publish_sidebar_toggle')?.addEventListener('click', function (event) {
            event.preventDefault();
            var target = document.querySelector('.publish_sidebar_toggle');
            var target1 = document.querySelector('.ec_sidebar');
            if (target.classList.contains('active')) {
                target.classList.remove('active');
                target1.classList.remove('active');
            } else {
                target.classList.add('active');
                target1.classList.add('active');
            }
        });
        this.getShoponData();
        this.selectedShopOn()
    }


    selectedShopOn = (val) => {
        const { shopOnList } = this.props.shopOnData
        if (val) this.setState({ selectedWall: null, selectLoader: true, activeGallery: {} })
        else {
            var webData = shopOnList?.find((x) => x.id == localStorage.getItem('shopOn_id'));
            this.setState({ selectedWall: webData ? webData : shopOnList && shopOnList[0] ? shopOnList[0] : '' })
        }
    }

    getShoponData = () => {
        const { shopOnData, allWalls } = this.props;
        var shopOnArr = shopOnData?.shopOnList?.find((item) => String(item.id) == String(localStorage.getItem('shopOn_id')));
        var shoponData = shopOnArr ? shopOnArr : shopOnData?.shopOnList ? shopOnData?.shopOnList[0] : ''
        var wallIdshop = shopOnArr && shopOnArr.wallId ? shopOnArr.wallId : allWalls && allWalls[0] ? allWalls[0].id : '';
        if (shoponData) {
            localStorage.setItem('shopOnActive_wall', wallIdshop)
            var gallery = allWalls?.find((item) => item.id == wallIdshop);
            this.setState({ title: shoponData?.extra_data?.title, description: shoponData?.extra_data?.description, profile: shoponData?.extra_data?.profile, slug: (shoponData?.slug).toLowerCase(), currentSlug: (shoponData?.slug).toLowerCase(), theme_type: shoponData?.extra_data?.theme_type, activeGallery: gallery, slugError: "", spinner: false, ifchanges: !shoponData.wallId, selectLoader: false }, () => {
                if (!shoponData?.slug) { this.checkSlugAvailability(shoponData?.extra_data?.title); this.setState({ footer_action2: false, ifchanges: shoponData.wallId ? false : true }) }
                else {
                    if (shoponData.wallId) this.setState({ footer_action2: true })
                    else this.setState({ footer_action2: false })
                }
                this.setShoponData(shoponData)
            })
            this.GETWALL_DATA(wallIdshop)
        }
    }

    setShoponData = (data) => {
        this.props.SHOPON_DESCRIPTION_REDUCER(data?.extra_data?.description)
        this.props.SHOPON_LOGO_REDUCER(data?.extra_data?.profile)
        this.props.SHOPON_TYPE_REDUCER(data?.extra_data?.theme_type)
        this.props.SHOPON_TITLE_REDUCER(data?.extra_data?.title)
    }

    GETWALL_DATA = (e) => {
        const { allWalls } = this.props
        var gallery = allWalls?.find((item) => item.id == e);
        this.setState({ activeGallery: gallery })
        localStorage.setItem("shopOnActive_wall", gallery?.id)
        this.props.CHANGE_SHOPON_WALL(gallery ? gallery.id : e, 21)
    }

    checkSlugAvailability = (e) => {
        if (e) {
            var slug = e?.toLowerCase()?.replace(/ /g, '-').replace(/[^\w-]+/g, '');
            if (!this.state.slug) this.setState({ currentSlug: slug })
            this.setState({ slug: slug, ifchanges: true }, () => {
                if (slug?.length >= 4) this.callSlug(slug)
                else this.setState({ slugError: 'error', slugMsg: 'Slug characters length should be minimum 4.' })
            })
        } else {
            this.setState({ slug: e, slugError: 'error', slugMsg: 'Slug characters length should be minimum 4.' })
        }
    }

    callSlug = debounce((slug) => {
        const shopOnId = localStorage.getItem('shopOn_id');
        SHOPON_SLUG_AVAILABILITY({ slug, id: shopOnId })
            .then(({ data: { responseCode, responseStatus, status, response_message, response_code } }) => {
                const slugError = status || responseStatus;
                this.setState({ slugError, slugMsg: response_message });

                if (responseCode === 200 || response_code === 400) {
                    this.setState({ slugError, slugMsg: response_message });
                }
            })
            .catch(error => console.log('error', error));
    }, 500);

    componentDidUpdate(prevProps) {

        if (!_.isEqual(prevProps.allWalls, this.props.allWalls)) this.getShoponData();

        if (!_.isEqual(this.props.shopOnData?.shopOnList, prevProps.shopOnData?.shopOnList)) {

            setTimeout(() => {
                this.getShoponData();
                this.selectedShopOn()
            }, 500);
        }

        if (localStorage.getItem('analytics_shopon')) {
            this.setState({ analytics_shopon: true })
            localStorage.removeItem('analytics_shopon')
        }
    }

    submitShopon = () => {
        const { title, ifchanges, description, profile, slug, theme_type } = this.state;
        if (ifchanges && slug.length > 2 && title && description && profile) {
            this.setState({ footer_action1: true, spinner: true })
            var obj = { action: 2, wallId: localStorage.getItem('shopOnActive_wall'), id: localStorage.getItem('shopOn_id'), title: title, description: description, profile: profile, theme_type: theme_type, slug: slug }
            this.props.UPDATE_SHOPON(obj, this.getResponse)
        }
        else {
            if (!title) this.setState({ errorTitle: true })
            if (!description) this.setState({ errorDescription: true })
        }
    }



    getResponse = (code, msg) => {
        this.setState({ slugError: false, spinner: false, ifchanges: false, currentSlug: this.state.slug })
        if (code == 200) this.setState({ footer_action2: true })
        else this.setState({ footer_action2: false })
    }

    changeTitle = (e) => {
        this.setState({ title: e, errorTitle: false, ifchanges: true })
        this.props.SHOPON_TITLE_REDUCER(e)
    }

    changeDescription = (e) => {
        this.setState({ description: e, errorDescription: false, ifchanges: true })
        this.props.SHOPON_DESCRIPTION_REDUCER(e)
    }

    changeProfile = (e) => {
        this.setState({ profile: e, ifchanges: true })
        this.props.SHOPON_LOGO_REDUCER(e)
    }

    changeTheme = (e) => {
        this.setState({ theme_type: e, ifchanges: true })
        this.props.SHOPON_TYPE_REDUCER(e)
    }

    copyText = () => {
        const { slug } = this.state
        navigator.clipboard.writeText(`https://${IS_API_ACCESS_LIVE ? '' : 'test.'}shopon.bio/${slug}`)
        this.setState({ copied: true }, () => setTimeout(() => {
            this.setState({ copied: false })
        }, 2000))
    }



    changDevice = e => {
        this.setState({
            isMobile: e == 1 ? true : false
        })
    }

    changeShopOn = (item) => {
        const { shopOnData, navigate, allWalls, activeShopOn } = this.props
        this.setState({ selectLoader: true, activeGallery: {}, selectedWall: null })
        localStorage.setItem('shopOn_id', item.id);
        navigate(`/shopon-bio/preview/${item.id}`)
        var webData = shopOnData?.shopOnList.find((x) => x.id == item.id);
        const gallery = allWalls.find(x => x.id == item.wallId)
        this.props.SHOPON_BRAND_DATA_REDUCER()
        var newwallID = webData?.wallId ? webData?.wallId : allWalls && allWalls.length > 0 ? allWalls[0].id : null;
        localStorage.setItem('shopOnActive_wall', newwallID)
        this.setState({ selectedWall: webData, activeGallery: gallery ? gallery : allWalls && allWalls.length > 0 ? allWalls[0] : null, selectLoader: false })
        this.getShoponData()
        this.props.CHANGE_SHOPON_WALL(newwallID, 20);

    }

    closeAside = () => this.setState({ filterToggle: false })

    render() {
        const { footer_action1, theme_type, activeGallery, ifchanges, currentSlug, slugError, analytics_shopon, selectedWall, isMobile, filterToggle, spinner, selectLoader } = this.state;
        const { allWalls, shopOnData, userData } = this.props;
        const authData = userData?.user_data?.sessionData
        const websiteUrl = authData?.createFrom.toLowerCase() == 'shopify' ? `shopify` : authData?.createFrom.toLowerCase() == 'bigcommerce' ? `bigcommerce` : 'publish'
        const { navigate } = this.props

        return (<div className='d-flex flex-column flex-column-fluid full-content'>
            <div className="subheader">
                <PublishSubheader websiteUrl={websiteUrl}/>
            </div>
            <Container fluid>
                {shopOnData && shopOnData?.shopOnList?.length ?
                    <Card className="content_inner shopon_bio border-0">
                        <Card.Body className='p-0 d-flex w-100'>
                            <CardAside filterToggle={filterToggle} closeAside={this.closeAside}>
                                <Card className='a_header_ a_footer_'>
                                    <Card.Header className='py-4'>
                                        <WebsiteSwitcher contentList={shopOnData?.shopOnList} changeWebsite={this.changeShopOn} selectedContent={selectedWall} title="ShopOn bio" method={this.selectedShopOn} addShopon={shopOnData.addShoponNew} />
                                    </Card.Header>
                                    <Card.Body className='bg-gray-100 aside_body p-0' style={{ marginBottom: -1 }}>
                                        <div className='border-bottom p-4 mb-4'>
                                            <h5>Select Content</h5>
                                            <ContentSwitcher contentList={allWalls} modifyContent={(data) => this.setState({ ifchanges: true }, () => this.GETWALL_DATA(data.id))} selectedContent={activeGallery} title="Shop" onlySwitcher={true} selectLoader={selectLoader} />
                                        </div>
                                        {footer_action1 ? <Aside {...this.state} changeTitle={(e) => this.changeTitle(e)} changeDescription={(e) => this.changeDescription(e)} changeProfile={(e) => this.changeProfile(e)} changeSlug={(e) => this.checkSlugAvailability(e)} changeTheme={(e) => this.changeTheme(e)} copyText={this.copyText} /> : ''}
                                    </Card.Body>
                                    <Card.Footer className='d-flex justify-content-end'>
                                        <div>
                                            <Button id='save_bio_btn' className={`my-1 ${spinner ? `spinner` : ''} ${ifchanges ? '' : 'disabled'} ${slugError == 'error' ? 'disabled' : ''}`} onClick={this.submitShopon}>Save</Button>
                                        </div>
                                    </Card.Footer>
                                </Card>
                            </CardAside>
                            <Card className="preview_ p_header_">
                                {shopOnData.wallData && shopOnData.wallData.length ?
                                    <Card.Header>
                                        <div className='f-between w-100'>
                                            <span className='h5 mb-0 '>Preview</span>
                                            <div className={`animated_btn-group rounded-2 ${isMobile ? 'active' : ''} d-none d-lg-block`}>
                                                <ToggleButtonGroup className={`f-center`} type="radio" name="device" value={isMobile ? 1 : 2} onChange={this.changDevice}>
                                                    <ToggleButton variant='animated' className={`text-nowrap ${!isMobile ? 'text-muted' : 'text-primary'}`} id="mobile-btn" value={1}>
                                                        <FontAwesomeIcon icon={'mobile'} />
                                                        Mobile
                                                    </ToggleButton>
                                                    <ToggleButton variant='animated' className={`text-nowrap ${isMobile ? 'text-muted' : 'text-primary'}`} id="laptop-btn" value={2}>
                                                        <FontAwesomeIcon icon={'desktop'} />
                                                        Desktop
                                                    </ToggleButton>
                                                </ToggleButtonGroup>
                                                <div className='animated_btn_bg'></div>
                                            </div>
                                            <div>
                                                <Button id="toggle_aside" variant='link' size='sm' className='d-flex d-xl-none' onClick={() => this.setState({ filterToggle: !filterToggle })}>
                                                    Menu
                                                    <FontAwesomeIcon icon={'sliders'} iconClass={'ms-2 lh-base'} />
                                                </Button>
                                            </div>
                                        </div>
                                    </Card.Header>
                                    : null}
                                <Card.Body className='f-center flex-wrap flex-column'>
                                    {shopOnData.loadingShopon || !allWalls || !selectedWall ?
                                        <Loader />
                                        :
                                        shopOnData.wallData && shopOnData.wallData.length ?
                                            <div id='shop_preview' className={`scale_preview_wrap ${isMobile ? 'mobile_active' : 'laptop_active'} ${theme_type == 2 ? 'bg-black' : 'bg-white'}`}>
                                                <div className={`scale_preview position-relative`}>
                                                    {isMobile ? <div className={`position-relative h-30px f-center`} style={{ zIndex: 2 }}>
                                                        <Image src={baseUrl(`media/images/mobile-bar-${theme_type == 2 ? 'dark' : 'light'}.svg`)} width={242} height={30} fluid /></div>
                                                        : null}
                                                    <iframe title='theme_live_preview' src={`${SHOPON_EMBED_URL}/${currentSlug}?editor=1`} id="shopOnBioPreview"></iframe>
                                                </div>
                                            </div>
                                            :
                                            <>

                                                {!shopOnData.loadingShopon ? <div className='position-absolute top-0 end-0 m-3'>
                                                    <Button id="toggle_aside" variant='link' size='sm' className='d-flex d-xl-none' onClick={() => this.setState({ filterToggle: !filterToggle })}>
                                                        Menu
                                                        <FontAwesomeIcon icon={'sliders'} iconClass={'ms-2 lh-base'} />
                                                    </Button>
                                                </div> : null}
                                                {allWalls && allWalls.length <= 0 ?
                                                    <EmptyWithIcon title={'Preview Missing? 👀'} desc={'You skipped the preliminary step. Connect your social account to collect content and begin creating your gallery.'} icon={'media'} button={{ action: () => this.props.navigate(`/content/moderation/${localStorage.getItem('wallId') ? localStorage.getItem('wallId') : ''}`), text: 'Add Content' }} isFullScreen={false} /> :

                                                    <EmptyWithVideo title={'Your Shopon.Bio Needs Content! 🤳'} desc={'Oops! Seems like your Shopon.Bio landing page is missing posts at the moment. To create a captivating landing page, add content to your gallery or make some posts public.'} vimeoCode={'989382372'} button={{ action: () => this.props.navigate(`/content/addfeed/${localStorage.getItem('wallId') ? localStorage.getItem('wallId') : ''}`), text: 'Add Content' }} isFullScreen={false} />}
                                            </>
                                    }
                                </Card.Body>
                            </Card>
                        </Card.Body>
                    </Card>
                    :

                    shopOnData.shopOnListLoader ?

                        <div style={{ minHeight: 'calc(100vh - 170px)' }}>
                            <Loader />
                        </div>

                        :
                        <div className='f-center' style={{ minHeight: 'calc(100vh - 50px)' }}>
                            <EmptyWithVideo title={'Create a ShopOn, to start'} desc={'Oops! Looks like you have deleted your ShopOn bio, create one by simply clicking on the button below.'} vimeoCode={'989382372'} button={{ action: () => this.props.TOGGLE_ADD_SHOPON(), text: 'Create ShopOn Bio' }} isFullScreen={true} />
                            <WebsiteSwitcher title="ShopOn bio" method={this.selectedShopOn} addNew={shopOnData.addShoponNew} noList={true} />
                        </div>
                }

            </Container>
        </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        allWalls: state.dashboardData?.dashboarddata?.allWalls,
        userData: state.authorized?.userData,
        shopOnData: state.shopOnList
    }
}
const mapDispatchToProps = dispatch => {
    return {
        CHANGE_SHOPON_WALL: (wallId, limit) => dispatch(CHANGE_SHOPON_WALL(wallId, limit)),
        UPDATE_SHOPON: (obj, callback) => dispatch(UPDATE_SHOPON(obj, callback)),
        getUserInfo_afterChanges: () => dispatch(getUserInfo_afterChanges()),
        TOGGLE_ADD_SHOPON: () => dispatch({ type: TOGGLE_ADD_SHOPON, payload: true }),
        SHOPON_TITLE_REDUCER: (data) => dispatch({ type: SHOPON_TITLE_REDUCER, payload: data }),
        SHOPON_DESCRIPTION_REDUCER: (data) => dispatch({ type: SHOPON_DESCRIPTION_REDUCER, payload: data }),
        SHOPON_LOGO_REDUCER: (data) => dispatch({ type: SHOPON_LOGO_REDUCER, payload: data }),
        SHOPON_TYPE_REDUCER: (data) => dispatch({ type: SHOPON_TYPE_REDUCER, payload: data }),
        GET_ALL_SHOPON_LIST: () => dispatch(GET_ALL_SHOPON_LIST()),
        SHOPON_BRAND_DATA_REDUCER: (data) => dispatch({ type: SHOPON_BRAND_DATA_REDUCER, payload: data }),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(withRouter(ShopOnBio));